<template>
  <FileUploader
    :autofocus="autofocus"
    :dense="dense"
    :temporary="temporary"
    :withItems="withItems"
    :miniItems="miniItems"
    :multiple="multiple"
    :note="note"
    :accept="accept"
    @added="(files) => $emit('added', files)"
    @uploaded="({ files, xhr }) => $emit('uploaded', { files, xhr })"
    @removed="(files) => $emit('removed', files)"
    :factory="factory"
    :removedFile="removedFile"
    :initSlots="initSlots"
    :thumbnail="thumbnail"
  />

  <q-checkbox
    autofocus
    v-model="checkboxModel"
    :val="description || desc"
    color="secondary"
    class="field-select text-body1 font-medium rounded bg-none text-primary-500 q-py-sm q-pl-sm full-width flex-label full-height q-mt-lg"
    @update:modelValue="(val) => $emit('checked', val[0])"
  >
    {{ description || desc }}
  </q-checkbox>
</template>

<script>
import { defineComponent, defineAsyncComponent } from 'vue';

export default defineComponent({
  name: 'WebsiteContent',

  emits: ['added', 'uploaded', 'removed', 'checked'],

  props: {
    note: {
      type: String,
      default: 'SVG, PNG, JPEG or GIF (max. 5MB)',
    },
    thumbnail: {
      type: [Boolean, String],
      default: false,
    },
    miniItems: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: false,
    },
    autofocus: {
      type: Boolean,
      default: false,
    },
    accept: {
      type: String,
      default: 'image/*, woff, woff2, otf, ttf, psd, eps, ai, xd, fig',
    },
    ['max-file-size']: {
      type: [Number, String],
      default: 10000000,
    },
    withItems: { type: Boolean, default: false },
    autoRemoveFailed: { type: Boolean, default: false },
    temporary: { type: Boolean, default: false },
    multiple: { type: Boolean, default: true },
    description: {},
    removedFile: {},
    initSlots: {},
    factory: {},
    checked: {
      type: [Array, String],
      default: () => [],
    },
  },

  components: {
    FileUploader: defineAsyncComponent(() =>
      import('src/components/Helpers/FilesUploader.vue')
    ),
  },

  data() {
    return {
      checkboxModel: [],
      desc: 'Please use placeholder content on the design concept',
    };
  },

  mounted() {
    if (this.checked?.length)
      this.checkboxModel =
        typeof this.checked === 'string' ? [this.checked] : this.checked;
  },
});
</script>
